const ContestsList = () => {
  // ** Store Vars
  const dispatch = useDispatch()
  const store = useSelector((state) => state.winner)
  const {quizdata} = store
  console.log(quizdata.links)
  // ** States
  const [searchTerm, setSearchTerm] = useState('')
  const [listData, setListData] = useState([])
  const [currentPage, setCurrentPage] = useState(1)
  //const [rowsPerPage, setRowsPerPage] = useState(15)
  // const [sidebarOpen, setSidebarOpen] = useState(false)
 

  // ** Get data on mount
  useEffect(() => {
    dispatch(getAllContestsQuiz(currentPage))
  }, [currentPage])

  useEffect(() => {
    console.log(store.quizdata.links)
    if (store.quizdata.data && store.quizdata.data.length) {
      setListData(store.quizdata.data)
    }
  }, [JSON.stringify(store.quizdata.data)]) 

  // useEffect(() => {
  //   if (store.feedAllData.records && store.feedAllData.records.length) {
  //     setListData(store.feedAllData.records)
  //   }
  // }, [JSON.stringify(store.feedAllData)])
 

  const handlePagination = (page) => {
    setCurrentPage(page.selected + 1)
   
  }

  // ** User filter options

  // ** Function in get data on page change

  // ** Function in get data on rows per page
  const handlePerPage = () => {
   // const value = parseInt(e.currentTarget.value)
    //setRowsPerPage(value)
    setCurrentPage(1)
  }

  // ** Function in get data on search query change
  const handleFilter = (val) => {
    setSearchTerm(val)
  }

  useEffect(() => {
    if (searchTerm) {
      const filterList = store.quizdata.filter((userItem) => {
        
        if (userItem.contest_name) {
          return userItem.contest_name.toLowerCase().includes(searchTerm.toLowerCase())
        }
      })

      setListData(filterList)
      setCurrentPage(1)
    } else {
      setListData(store.quizdata)
    }
  }, [searchTerm])

  // ** Custom Pagination
  const CustomPagination = () => {
    const count = Number(Math.ceil(store.quizdata.links.length))
    //const count = store.allCricketdata.links.length
    return (
      <ReactPaginate
        previousLabel={''}
        nextLabel={''}
        pageCount={count || 1}
        activeClassName="active"
        forcePage={currentPage !== 0 ? currentPage - 1 : 0}
        onPageChange={(page) => handlePagination(page)}
        pageClassName={'page-item'}
        nextLinkClassName={'page-link'}
        nextClassName={'page-item next'}
        previousClassName={'page-item prev'}
        previousLinkClassName={'page-link'}
        pageLinkClassName={'page-link'}
        containerClassName={
          'pagination react-paginate justify-content-end my-2 pr-1'
        }
      />
    )
  }

  // ** Table data to render
  const dataToRender = () => {
    const filters = {
      q: searchTerm
    }

    const isFiltered = Object.keys(filters).some(function (k) {
      return filters[k].length > 0
    })
    // if (store.allContests.length > 0) {
    //   return store.allContests
    // } else
    if (listData.length === 0 && isFiltered) {
      return []
    } else {
      return listData
    }
  }

  return (
    <Fragment>
      <Card>
        <DataTable
          noHeader
          pagination
          subHeader
          responsive
          paginationServer
          columns={columns}
          paginationPerPage
          paginationDefaultPage={currentPage}
          sortIcon={<ChevronDown />}
          className="react-dataTable"
          paginationComponent={CustomPagination}
          data={dataToRender()}
          subHeaderComponent={
            <CustomHeader
              handlePerPage={handlePerPage}
              rowsPerPage
              searchTerm={searchTerm}
              handleFilter={handleFilter}
            />
          }
        />
      </Card>
    </Fragment>
  )
}

export default ContestsList
