import http from '../../../../configs/httpConfig'
export const getAllWallDataFail = () => ({
  type: 'GET_ALL_WALL_DATA_FAIL'
})
export const getAllWallDataInit = () => ({
  type: 'GET_ALL_WALL_DATA_INIT'
})


export const getAllContests = () => {
 
  return async (dispatch) => {
    dispatch(getAllWallDataInit())
    await http.get('/reports/referralReport').then((response) => {
      console.log("Ajit", response.data)
      dispatch({
        type: 'GET_ALL_CRICKET_CONTESTS_REPORT',
        data: response.data.data
      })
    }).catch((e) => {
      console.log(e)
      dispatch(getAllWallDataFail())
    })
  }
}
export const getAllContestsWinner = (page) => {
  return async (dispatch) => {
    const url = `/reports/redeemReport?page=${page}`
    await http.get(url).then((response) => {
      console.log(response.data)
      dispatch({
        type: 'GET_ALL_WINNER_CONTESTS_REPORT',
        data: response.data
      })
    })
  }
}
